body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 62.5%;
  font-family: Helvetica, Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#landingPage {
  height:100vh;
  width: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shapeObjectTitle {
  width: 60%;
}

.shapeObject {
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shapeObject:hover {
  cursor: pointer;
}

.clickable {
  font-size: 1rem;
}

.relative {
  position: relative;
  overflow: auto
}

@media only screen and (max-width: 400px) {
  #desktopSite {
    display: none;
  }
}


#landingPage {
    height:100vh;
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
#leadingBall {
    height: 8rem;
    width: 8rem;
    background-color: black;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.clickable {
    font-size: 1rem;
}
#triangle {
    height: 10rem;
    width:  10rem;
    background-color: white;
}

.bdayGirlPicture {
    object-fit: cover;
    width: 100%;
    aspect-ratio: 10/15;
}

.bdayGirlPictureContainer {
    padding: 3rem 2rem;
    box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 1);
    background-color: white;
}

